<template>
  <div style="min-width: 1000px;">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>应用用户</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-button type="primary" @click="addDialogVisible = true">添加</el-button>
      </el-row>
      <el-table :data="userlist" stripe>
        <el-table-column type="index" label="序号"></el-table-column>
        <el-table-column label="应用名" prop="userName" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="appKey" prop="appKey" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{ scope.row.appKey === '' || scope.row.appKey == null ? '-' : scope.row.appKey }}</span>
          </template>
        </el-table-column>
        <el-table-column label="appSecret" prop="appSecret" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span v-if="isShow === scope.row.appSecret">{{ scope.row.appSecret === '' || scope.row.appSecret == null ? '-' : scope.row.appSecret }}</span>
            <span v-else>******</span>
          </template>
        </el-table-column>
        <el-table-column label="备注" prop="commitInfo" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{ scope.row.commitInfo === '' ||  scope.row.commitInfo == null ? '-' : scope.row.commitInfo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="创建时间" prop="updateTime" :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button type="danger" size="mini" @click="deleteByName(scope.row.userName)">删除</el-button>
            <el-button type="primary" size="mini" @click="lookBySecret(scope.row.appSecret)">显示</el-button>
            <el-button type="info" size="mini" @click="unlookBySecret">隐藏</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5,10,15,20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <el-dialog title="添加" :visible.sync="addDialogVisible" width="520px" @close="addDialogClosed">
      <el-form style="width: 80%;margin: 20px auto;" :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
        <el-form-item label="应用名" prop="loginName">
          <el-input v-model.trim="addForm.loginName"></el-input>
        </el-form-item>
        <!--<el-form-item label="密码" prop="loginPass">-->
        <!--<el-input type="password" v-model.trim="addForm.loginPass"></el-input>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="确认密码" prop="loginPass2">-->
        <!--<el-input type="password" v-model.trim="addForm.loginPass2"></el-input>-->
        <!--</el-form-item>-->
        <el-form-item label="备注" prop="commitInfo">
          <el-input type="textarea" maxlength="200" show-word-limit :rows="5" v-model.trim="addForm.commitInfo"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" v-preventReClick="1000" @click="submitAdd">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      const keyBlur = (rule, val, cb) => {
        const reg = /^[a-zA-Z0-9_]{4,16}$/;
        if (reg.test(val)) {
          this.getExternal(val, cb)
        } else {
          cb(new Error('请输入数字、字母、下划线组成的字符，长度为4-16位'))
        }
      };
      const valiPws2 = (rule, value, cb) => {
        if (value === '') {
          cb(new Error('请再次输入密码'));
        } else if (value !== this.addForm.loginPass) {
          cb(new Error('两次输入密码不一致'));
        } else {
          cb();
        }
      };
      return {
        isShow:'',
        userlist: [],
        queryInfo: {
          pagenum: 1,
          pagesize: 10
        },
        total: 0,
        addDialogVisible: false,
        addForm: {
          loginName: '',
          loginPass: '',
          loginPass2: '',
          commitInfo: ''
        },
        addFormRules: {
          loginName: [{
            required: true,
            validator: keyBlur,
            trigger: 'blur'
          }],
          // loginPass: [{
          //   required: true,
          //   validator: this.Global.valiPass,
          //   trigger: 'blur'
          // }],
          // loginPass2: [{required: true, validator: valiPws2, trigger: 'blur'}],
          commitInfo: [{min: 0, max: 200, message: '长度在200个字符以内', trigger: 'blur'}]
        },
      }
    },
    beforeRouteEnter: (to, form, next) => {
      const loginType = window.sessionStorage.getItem('accountTypeLogin');
      if (loginType === '9') {
        next()
      }else{
        return next('/404')
      }
    },
    created() {
      this.getUserList();
    },
    methods: {
      async getUserList() {
        const {data: res} = await this.$http.get(`v1/kms/ListUser/${this.queryInfo.pagenum}/${this.queryInfo.pagesize}?ts=${new Date().getTime()}`);
        if (res.code !== 0) return this.$message.error('获取用户信息失败！');
        this.userlist = res.data.list;
        this.total = res.data.total;
      },
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize;
        this.getUserList()
      },
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage;
        this.getUserList()
      },
      lookBySecret(name){
        this.isShow = name;
      },
      unlookBySecret(){
        this.isShow = '******'
      },
      addDialogClosed() {
        this.options = [];
        this.$refs.addFormRef.resetFields();
      },
      submitAdd() {
        this.$refs.addFormRef.validate(async valid => {
          if (!valid) return;
          const {data: resReg} = await this.$http.post('v1/kms/AddUser', {
            'userName': this.addForm.loginName,
            'loginPass': 'a1234567',
            'commitInfo': this.addForm.commitInfo,
          });
          if (resReg.code !== 0) return this.$message.error('添加失败!');
          this.$message.success('添加成功!');
          this.addDialogVisible = false;
          this.getUserList()
        })
      },
      async deleteByName(name) {
        const confirmResult = await this.$confirm(
          '此操作将删除该用户，是否继续？', '提示', {
            confirmButtonText: '确定',
            cancelButtontext: '取消',
            type: 'warning',
          }
        ).catch(err => err);
        if (confirmResult !== 'confirm') {
          return
        }
        const {data: res} = await this.$http.post('v1/kms/DelUser', {
          'userName': name
        });
        if (res.code !== 0) return this.$message.error('删除失败！');
        this.$message.success('删除成功！');
        this.getUserList()
      },
      async getExternal(val, cb) {
        const {data: res} = await this.$http.post('v1/kms/preRegister', {
          'userName': this.addForm.loginName
        });
        if (res.code !== 0) return cb(res.msg);
        return cb()
      }
    }
  }
</script>
<style lang="less" scoped>
  .el-select {
    width: 100%;
  }

  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 120px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }

  .el-form--inline .el-form-item__content > span {
    display: inline-block;
    max-width: 500px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
</style>
